import $ from 'jquery';
import 'select2';
import 'magnific-popup';

export default {

	init() {
		$('.select').select2({
			width : "resolve",
			dropdownParent: $('#modal .modal__body'),
			closeOnSelect: false
		});

		$('.select').on('select2:open', function(e) {
			const modal = $('.modal_mobile-select');
			const dropdown = modal.find('.select2-dropdown');
			console.log(modal.find('li').length);
			modal.find('.select2-container').css({
				position : 'relative',
				top : 0,
				left : 0
			});

			dropdown.css({
				position : 'relative',
				top : 0,
				left : 0
			});

			$.magnificPopup.open({
				items: {
					src: modal, // can be a HTML string, jQuery object, or CSS selector
					type: 'inline'
				},
				callbacks : {
					open: function() {
						modal.find('.select2-container').css({
							position : 'relative',
							top : 0,
							left : 0
						});
					},
					markupParse: function(template, values, item) {
						modal.find('.select2-container').css({
							position : 'relative',
							top : 0,
							left : 0
						});
					}
				}
			});
			
		})
	}

};


